<script>
  import Breakpoint from '../../util/breakpoint'
  import Themeable from '../../mixins/themeable'

  import Resize from '../../directives/resize'

  export default {
    name: 'v-app',

    mixins: [Breakpoint, Themeable],

    directives: {
      Resize
    },

    data: () => ({
      resizeTimeout: {}
    }),

    props: {
      fixedFooter: Boolean,
      footer: Boolean,
      id: {
        type: String,
        default: 'app'
      },
      toolbar: Boolean
    },

    methods: {
      resizeDebounce () {
        clearTimeout(this.resizeTimeout)

        this.resizeTimeout = setTimeout(this.onResize, 200)
      }
    },

    mounted () {
      this.onResize()
    },

    render (h) {
      const data = {
        staticClass: 'application',
        'class': {
          'application--dark': this.dark,
          'application--light': !this.dark,
          'application--footer': this.footer || this.fixedFooter,
          'application--footer-fixed': this.fixedFooter,
          'application--toolbar': this.toolbar
        },
        attrs: { 'data-app': true },
        domProps: { id: this.id },
        directives: [{
          name: 'resize',
          value: this.resizeDebounce
        }]
      }

      return h('div', data, this.$slots.default)
    }
  }
</script>

<style lang="stylus" src="../../stylus/components/_app.styl">
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: #000
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom
</style>



