<script>
  import ButtonGroup from '../../mixins/button-group'

  export default {
    name: 'v-bottom-nav',

    mixins: [ButtonGroup],

    props: {
      absolute: Boolean,
      active: [Number, String],
      shift: Boolean,
      value: { required: false }
    },

    watch: {
      active () {
        this.update()
      }
    },

    computed: {
      classes () {
        return {
          'bottom-nav': true,
          'bottom-nav--absolute': this.absolute,
          'bottom-nav--shift': this.shift,
          'bottom-nav--active': this.value
        }
      }
    },

    methods: {
      isSelected (i) {
        const item = this.getValue(i)
        return this.active === item
      },
      updateValue (i) {
        const item = this.getValue(i)
        this.$emit('update:active', item)
      }
    },

    render (h) {
      return h('div', {
        class: this.classes
      }, this.$slots.default)
    }
  }
</script>

<style lang="stylus" src="../../stylus/components/_bottom-navs.styl">
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: #000
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom
</style>



