<script>
  import Themeable from '../../mixins/themeable'

  export default {
    name: 'v-list',

    provide () {
      return {
        listClick: this.listClick,
        listClose: this.listClose
      }
    },

    mixins: [Themeable],

    data () {
      return {
        uid: null,
        groups: []
      }
    },

    props: {
      dense: Boolean,
      subheader: Boolean,
      threeLine: Boolean,
      twoLine: Boolean
    },

    computed: {
      classes () {
        return {
          'list': true,
          'list--two-line': this.twoLine,
          'list--dense': this.dense,
          'list--three-line': this.threeLine,
          'list--subheader': this.subheader,
          'theme--dark dark--bg': this.dark,
          'theme--light light--bg': this.light
        }
      }
    },

    watch: {
      uid () {
        this.$children.filter(i => i.$options._componentTag === 'v-list-group').forEach(i => i.toggle(this.uid))
      }
    },

    methods: {
      listClick (uid, force) {
        if (force) {
          this.uid = uid
        } else {
          this.uid = this.uid === uid ? null : uid
        }
      },

      listClose (uid) {
        if (this.uid === uid) {
          this.uid = null
        }
      }
    },

    render (h) {
      const data = {
        'class': this.classes,
        attrs: { 'data-uid': this._uid }
      }

      return h('ul', data, [this.$slots.default])
    }
  }
</script>

<style lang="stylus" src="../../stylus/components/_lists.styl">
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: #000
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom
</style>



