<script>
  import Themeable from '../../mixins/themeable'

  export default {
    name: 'v-subheader',

    functional: true,

    mixins: [Themeable],

    props: {
      inset: Boolean
    },

    render (h, { data, children, props }) {
      data.staticClass = (`subheader ${data.staticClass || ''}`).trim()

      if (props.inset) data.staticClass += ' subheader--inset'
      if (props.light) data.staticClass += ' theme--light'
      if (props.dark) data.staticClass += ' theme--dark'

      return h('li', data, children)
    }
  }
</script>

<style lang="stylus" src="../../stylus/components/_subheaders.styl">
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: #000
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom
</style>



