<script>
  import Themeable from '../../mixins/themeable'

  export default {
    name: 'v-card',

    functional: true,

    mixins: [Themeable],

    props: {
      flat: Boolean,
      height: {
        type: String,
        default: 'auto'
      },
      hover: Boolean,
      img: String,
      raised: Boolean,
      tile: Boolean
    },

    render (h, { data, props, children }) {
      data.staticClass = (`card ${data.staticClass || ''}`).trim()
      data.style = data.style || {}
      data.style.height = props.height

      if (props.flat) data.staticClass += ' card--flat'
      if (props.horizontal) data.staticClass += ' card--horizontal'
      if (props.hover) data.staticClass += ' card--hover'
      if (props.raised) data.staticClass += ' card--raised'
      if (props.tile) data.staticClass += ' card--tile'
      if (props.light) data.staticClass += ' theme--light'
      if (props.dark) data.staticClass += ' theme--dark'

      if (props.img) {
        data.style.background = `url(${props.img}) center center / cover no-repeat`
      }

      return h('div', data, children)
    }
  }
</script>

<style lang="stylus" src="../../stylus/components/_cards.styl">
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: #000
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom
</style>



