<script>
  export default {
    name: 'v-avatar',

    functional: true,

    props: {
      size: {
        type: String,
        default: '48px'
      },
      tile: Boolean
    },

    render (h, { data, props, children }) {
      data.staticClass = (`avatar ${data.staticClass || ''}`).trim()
      data.style = data.style || {}

      if (props.tile) data.staticClass += ' avatar--tile'

      data.style.height = props.size
      data.style.width = props.size

      return h('div', data, children)
    }
  }
</script>

<style lang="stylus" src="../../stylus/components/_avatars.styl">
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: #000
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom
</style>



